<template>
  <div>
    <form-wizard
      ref="formWizardNumRef"
      color="#7367F0"
      :title="null"
      :subtitle="null"
      shape="circle"
      class="mb-3 steps-transparent"
      :class="{ 'hidden-step': loginForm || selectBuildingForm }"
      :hide-buttons="false"
      @on-complete="formSubmitted"
    >
      <!-- account details tab -->
      <tab-content :before-change="validationForm">
        <b-card>
          <b-row>
            <b-col
              cols="12"
              class="mb-2"
            >
              <h5
                style="margin-bottom: 20px !important;    font-size: 20px;"
              >
                به شارژ مستر خوش آمدید
              </h5>
              <small class="text-muted">
                همین حالا شروع کنید تا ساختمان خود را به راحتی مدیریت کنید.
              </small>
            </b-col>
            <b-col
              v-if="!showEnterCodeField"
              md="12"
            >
              <b-form-group
                label="شماره تلفن همراه"
                label-for="mobile"
              >
                <validation-provider
                  #default="{ errors }"
                  name="Mobile"
                  rules="required|mobile"
                  :custom-messages="{ required: 'لطفا موبایل خود را وارد نمایید', mobile: 'لطفا موبایل خود را به درستی وارد نمایید' }"
                >
                  <b-form-input
                    ref="mobileInputRef"
                    id="mobile"
                    v-model="form.mobile"
                    type="tel"
                    @keyup.enter="goToNextStep"
                    placeholder="09xxxxxxxxx"
                    maxlength="11"
                    style="text-align: left; font-weight: 300; letter-spacing: 0.1em; font-size: 120%; direction: ltr; padding-top: 10px;"
                  />
                  <small class="text-danger" style="display: block; min-height: 1.4em;">{{ errors[0] }}</small>
                </validation-provider>
              </b-form-group>
            </b-col>
            <b-col
              v-if="showEnterCodeField"
              md="12"
            >
              <b-form-group
                label="کد تایید"
                label-for="confirmCode"
              >
                <b-form-input
                  id="confirmCode"
                  ref="confirmCodeRef"
                  v-model="confirmCode"
                  type="text"
                  placeholder="12345"
                  autocomplete="off"
                  maxlength="5"
                  style="text-align: center; direction: ltr"
                  @keyup.enter.prevent="goToNextStep"
                />
              </b-form-group>
            </b-col>
          </b-row>
<!--                    <FormWizardNumberButtons-->
<!--                      v-if="showEnterCodeField"-->
<!--                        :loading="true"-->
<!--                        :next-click="goToNextStep"-->
<!--                    />-->
          <button
            v-if="!loading"
            @click="goToNextStep"
            class="btn btn-primary btn-block mb-1 mt-5"
          >
            ادامه
          </button>
          <button v-else-if="loading  && !showEnterCodeField" class="btn btn-primary btn-block mb-1 mt-5" type="button" disabled>
            <span class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
            ادامه
          </button>
          <button
            v-else
            @click="goToNextStep"
            class="btn btn-primary btn-block mb-1 mt-5"
          >
            کد
          </button>
          <button
            v-if="!showEnterCodeField && !loginForm"
            @click="showDemo"
            class="btn btn-primary btn-block mb-1 mt-5"
            style="background: white !important; color: #7367f0 !important; visibility: hidden;"
          >
            مشاهده دمو
          </button>
        </b-card>
      </tab-content>
      <!-- personal details tabs -->
      <tab-content :before-change="validationFormApartmentInfo" v-if="!loginForm && !selectBuildingForm">
        <b-card>
          <b-row>
            <b-col
              cols="12"
              class="mb-2"
            >
              <h5 class="mb-0">
                اطلاعات مجتمع را وارد کنید
              </h5>
            </b-col>
            <b-col md="12">
              <b-form-group
                label="نام و نام خانوادگی مدیر"
                label-for="apartment-name"
              >
                <b-form-input
                  id="name"
                  v-model="form.name"
                  placeholder=""
                />
              </b-form-group>
            </b-col>
            <b-col md="12">
              <b-form-group
                label="نام مجتمع"
                label-for="apartment-name"
              >
                <b-form-input
                  id="apartment-name"
                  v-model="form.apartment.name"
                  placeholder=""
                />
              </b-form-group>
            </b-col>
            <b-col md="12">
              <b-form-group
                label="آدرس مجتمع"
                label-for="apartment-address"
              >
                <b-form-textarea
                  id="apartment-address"
                  v-model="form.apartment.address"
                  placeholder=""
                  rows="3"
                />
              </b-form-group>
            </b-col>
          </b-row>
          <FormWizardNumberButtons :next-click="goToNextStep"/>
        </b-card>
      </tab-content>
      <tab-content :before-change="validationFormApartmentUnit" v-if="!loginForm && !selectBuildingForm">
        <b-card>
          <b-row>
            <b-col
              cols="12"
              class="mb-2"
            >
              <h5 class="mb-0">
                تعداد طبقات و واحد‌های هر طبقه را مشخص کنید
              </h5>
            </b-col>
            <b-col md="12">
              <b-form-group
                label="تعداد طبقات"
                label-for="apartment-floors"
              >
                <b-form-input
                  id="apartment-floors"
                  v-model="form.apartment.floors"
                  type="number"
                  min="1"
                  max="20"
                  placeholder=""
                />
              </b-form-group>
            </b-col>
            <b-col md="12">
              <b-form-group
                label="شروع طبقات از"
                label-for="apartment-floors-start-at"
              >
                <v-select
                  :searchable="false"
                  id="apartment-floors-start-at"
                  v-model="form.apartment.floorsStartAt"
                  label="title"
                  :options="apartmentFloorsStartAtOptions"
                />
              </b-form-group>
            </b-col>
          </b-row>
          <b-button
            variant="primary"
            @click="addApartmentFloors"
          >
            ثبت
          </b-button>
        </b-card>
        <b-card v-show="showFloorsDetail && form.apartment.floors > 0">
          <h5>تعداد واحد‌های هر طبقه را مشخص کنید</h5>
          <div>
            <div
              v-for="(item,index) in floorsName"
              :key="index"
              class="d-flex justify-content-between align-items-center my-2"

            >
              <span style="margin-left: 10px">{{ item }} از واحد</span>
              <b-form-input
                v-model="form.apartment.unitsPerFloor[index].from"
                type="number"
                class="d-inline"
                style="width: 20%; font-size: 13px"
              />
              <span style="margin-left: 10px;">تا واحد</span>
              <b-form-input
                v-model="form.apartment.unitsPerFloor[index].to"
                type="number"
                class="d-inline"
                style="width: 20%; font-size: 13px"
              />
            </div>
          </div>
          <FormWizardNumberButtons :next-click="goToNextStep"/>
        </b-card>
      </tab-content>
      <tab-content :before-change="validationFormApartmentInfo" v-if="!loginForm && selectBuildingForm">
        <validation-observer
          ref="selectBuilding"
          tag="div"
        >
          <b-card>
            <b-row>
              <b-col cols="12" class="mb-5">
                <h5 class="mb-1">
                  به شارژ مستر خوش آمدید
                </h5>
                <small>{{ selectBuildingSubMessage }}</small>
              </b-col>
              <b-col v-for="building in buildingList" :key="building.building_id" md="12">
                <b-button
                  type="submit"
                  variant="outline-secondary"
                  block
                  @click="selectBuilding(building.building_id)"
                  class="mb-1 text-left"
                >

                  <h5>{{ building.name }}</h5>

                  <small style="display: block;" class="mt-1">
                    {{ building.address }}
                  </small>
                </b-button>
              </b-col>
              <b-col class="mb-5">&nbsp;</b-col>
            </b-row>
            <b-button
              variant="primary"
              block
              v-if="newBuilding"
              @click="submitNewBuilding"
            >
              ثبت ساختمان جدید
            </b-button>
          </b-card>
        </validation-observer>
      </tab-content>

      <template
        slot="footer"
        slot-scope="props"
      >
        <p
          v-if="props"
          style="visibility: hidden"
        />
      </template>
    </form-wizard>
  </div>
</template>

<script>
import { FormWizard, TabContent } from 'vue-form-wizard'
import vSelect from 'vue-select'
import { ValidationProvider, ValidationObserver } from 'vee-validate'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'
import 'vue-form-wizard/dist/vue-form-wizard.min.css'
import { v4 } from 'uuid'
import {
    BRow,
    BCol,
    BForm,
    BFormGroup,
    BFormInput,
    // BFormInvalidFeedback,
    BButton,
    BCard,
    BFormTextarea,
    BFormRadio,
} from 'bootstrap-vue'
import { required, email } from '@validations'
import { formatDate } from '@/@core/utils/filter'
import { codeIcon } from './code'
import FormWizardNumberButtons from './FormWizardNumberButtons.vue'
import axios from "@/libs/axios";

export default {
    components: {
        ValidationProvider,
        ValidationObserver,
        FormWizard,
        TabContent,
        BRow,
        BCol,
        BForm,
        BFormGroup,
        BFormInput,
        vSelect,
        // BFormInvalidFeedback,
        BButton,
        BCard,
        BFormTextarea,
        BFormRadio,
        // eslint-disable-next-line vue/no-unused-components
        ToastificationContent,
        FormWizardNumberButtons,
    },
    data() {
        return {
            loading: false,
            showFloorsDetail: false,
            loginForm: true,
            selectBuildingForm: false,
            selectBuildingSubMessage: '',
            newBuilding: false,
            buildingList: [],
            activeTabIndex: '',
            liteStructureGet: [],
            liteUserGet: [],
            form: {
                name: '',
                mobile: '',
                apartment: {
                    name: '',
                    address: '',
                    floors: '',
                    floorsStartAt: '',
                    unitsPerFloor: [],
                    ownersInfo: [],
                },
            },
            required,
            email,
            codeIcon,
            apartmentFloorsStartAtOptions: [
                {
                    title: 'طبقه اول',
                    value: 1
                },
                {
                    title: 'همکف',
                    value: 0
                },
                {
                    title: 'زیرزمین',
                    value: -1
                },
            ],
            showSetUnitOfFloorsCard: false,
            showEnterCodeField: false,
            confirmCode: '',
            token: '',
            floorsName: [],
        }
    },
    computed: {
        allUnitsPerFLoor() {
            const floors = []
            for (let i = 1; i <= +this.form.apartment.floors; i += 1) {
                floors.push({
                    floor: i,
                    units: this.form.apartment.ownersInfo.filter(
                        info => +info.floor === i,
                    ),
                })
            }

            return floors
        },
    },
    watch: {
        'form.apartment.floors': {
            handler(newValue) {
                this.resetApartmentFloors(newValue)
            },
        },
        liteStructureGet: {
            handler(newVal) {
                newVal.forEach(unitItem => {
                    unitItem.units.forEach(item => {
                        if (item.isOwner) {
                            // eslint-disable-next-line no-param-reassign
                            item.data.tenant.full_name = ''
                            // eslint-disable-next-line no-param-reassign
                            item.data.tenant.mobile = ''
                        }
                    })
                })
            },
            deep: true,
        },
        activeTabIndex(val) {
            this.$emit('stepChange', val)
        },
    },
    async mounted() {
        this.activeTabIndex = this.$refs.formWizardNumRef.activeTabIndex
        this.loginForm = document && document.location.href.indexOf("login") > -1
    },
    methods: {
        submitNewBuilding() {
            this.selectBuildingForm = false
            this.$refs.formWizardNumRef.reset()
            this.$refs.formWizardNumRef.activateTab(1)
            this.$refs.formWizardNumRef.activeTabIndex = 1
        },
        v4,
        showDemo() {
            axios
                .get('//sm.oop.ir/api/v1/auth/demo', {})
                .then(response => {
                    localStorage.setItem('token', response.data.token)
                    this.$router.push({ name: response.data.redirect })
                })
                .finally(() => {
                    }
                )
        },
        async formSubmitted() {
            const isSuccess = await this.$refs.accountRules.validate()
            if (!isSuccess) {
                return false
            }

            return true
        },
        async selectBuilding(id) {
            try {
                const { data } = await this.$http.post(
                    'https://sm.oop.ir/api/v1/auth/select-building',
                    {
                        mobile: this.form.mobile,
                        code: this.confirmCode,
                        manager: 1,
                        building_id: id,
                    },
                )
                if (!data.token) {
                    this.$toast({
                        component: ToastificationContent,
                        props: {
                            title: 'کد وارد شده اشتباه است',
                            icon: 'XCircle',
                            variant: 'error',
                        },
                    })
                    return false
                }
                localStorage.removeItem('token')
                localStorage.setItem('token', data.token)
                this.selectBuildingForm = false
                this.$refs.formWizardNumRef.reset()
                console.log('redirect to', data.redirect)
                switch (data.redirect) {
                    case 'create-building':
                        setTimeout(() => {
                            this.$refs.formWizardNumRef.activateTab(1)
                            this.$refs.formWizardNumRef.activeTabIndex = 1
                        }, 50)
                        break

                    case 'create-unit':
                        setTimeout(() => {
                            this.$refs.formWizardNumRef.activateTab(2)
                            this.$refs.formWizardNumRef.activeTabIndex = 2
                            console.log('--', this.$refs)
                        }, 50)
                        break

                    case 'register-dashboard' :
                        setTimeout(() => {
                            document.location.href = '/owner-resident'
                        }, 50)
                        break

                    case 'home' :
                        setTimeout(() => {
                            document.location.href = '/'
                        }, 50)
                        break
                }

                return true
            } catch (err) {
                this.$toast({
                    component: ToastificationContent,
                    props: {
                        title: err.response.data.message,
                        icon: 'XCircle',
                        variant: 'danger',
                    },

                })
                return false
            }
        },
        async validationForm() {
            // const isSuccess = await this.$refs.accountRules.validate()
            // if (!isSuccess) {
            //   return false
            // }

            try {
                if (this.showEnterCodeField) {
                    try {
                        const { data } = await this.$http.post(
                            'https://sm.oop.ir/api/v1/auth/verify',
                            {
                                mobile: this.form.mobile,
                                code: this.confirmCode,
                                manager: 1,
                            },
                        )
                        if (localStorage.getItem('token')) {
                            localStorage.removeItem('token')
                            localStorage.setItem('token', data.token)
                        } else {
                            localStorage.setItem('token', data.token)
                        }
                        this.token = data.token

                        console.log(data)

                        // todo: set Axios bearer token here
                        this.$toast({
                            component: ToastificationContent,
                            props: {
                                title: 'ورود با موفقیت انجام شد',
                                icon: 'XCircle',
                                variant: 'success',
                            },
                        })
                        this.form.name = data.name
                        switch (data.redirect) {
                            case ':dashboard':
                                // this.$router.push({name: 'home'})
                                document.location.href = '/'
                                return true

                            case ':step-2':
                                return true

                            case 'select-building':
                                this.selectBuildingForm = true
                                this.loginForm = false
                                this.selectBuildingSubMessage = data.message
                                this.newBuilding = data.new_building
                                this.buildingList = data.building_list ? data.building_list : []
                                break
                        }
                        return true
                        // resolve(true)
                    } catch (err) {
                        this.$toast({
                            component: ToastificationContent,
                            props: {
                                title: err.response.data.message,
                                icon: 'XCircle',
                                variant: 'danger',
                            },

                        })
                        this.$refs.confirmCodeRef.focus()
                        return false
                    }
                }
                const formData = new FormData()
                formData.append('mobile', this.form.mobile)
                // formData.append('name', this.form.name)
                this.loading = true
                await this.$http.post(
                    'https://sm.oop.ir/api/v1/auth/' + (this.loginForm ? 'login' : 'register'),
                    formData,
                )
                this.$toast({
                    component: ToastificationContent,
                    props: {
                        title: 'کد تایید ارسال شد',
                        icon: 'CheckCircle',
                        variant: 'success',
                    },
                })
                this.showEnterCodeField = true
                this.$nextTick(() => {
                    this.$refs.confirmCodeRef.focus()
                })

                return false
            } catch (err) {
                console.log(err)
                this.showEnterCodeField = false
                return false
            }
        },
        async validationFormApartmentInfo() {
            // // eslint-disable-next-line no-constant-condition, no-self-compare
            // if (1 === 1) {
            //   return true
            // }
            // const isSuccess = await this.$refs.infoRules.validate()
            // if (!isSuccess) {
            //   return false
            // }

            try {
                const formData = new FormData()
                formData.append('full_name', this.form.name)
                formData.append('building_name', this.form.apartment.name)
                formData.append('building_address', this.form.apartment.address)

                const res = await this.$http.post(
                    'https://sm.oop.ir/api/v1/building/lite-register-store',
                    formData,
                    {
                        headers: {
                            'Authorization': 'Bearer ' + localStorage.getItem('token'),
                        },
                    }
                )

                this.$toast({
                    component: ToastificationContent,
                    props: {
                        title: res.data.message,
                        icon: 'XCircle',
                        variant: 'success',
                    },
                })
                return true
                // resolve(true)
            } catch (err) {
                this.$toast({
                    component: ToastificationContent,
                    props: {
                        title: err.response.data.message,
                        icon: 'XCircle',
                        variant: 'danger',
                    },
                })
                return false
            }
        },
        async validationFormApartmentUnit() {
            // // eslint-disable-next-line no-constant-condition, no-self-compare
            // if (1 === 1) {
            //   return true
            // }

            // const isSuccess = await this.$refs.unitRules.validate()
            // // console.log('isSuccess', isSuccess)
            // if (!isSuccess) {
            //   return false
            // }

            try {
                // const formData = new FormData()
                // formData.append('building_name', this.form.apartment.name)
                // formData.append('building_address', this.form.apartment.address)
                const units = this.form.apartment.unitsPerFloor.map(item => [
                    item.from,
                    item.to,
                ])
                const data = {
                    floor_count: this.form.apartment.floors,
                    start_floor: this.form.apartment.floorsStartAt.value,
                    unit: [...units],
                }

                const res = await this.$http.post(
                    'https://sm.oop.ir/api/v1/building/lite-structure-store',
                    data,
                    {
                        headers: {
                            'Authorization': 'Bearer ' + localStorage.getItem('token'),
                        },
                    }
                )
                this.$toast({
                    component: ToastificationContent,
                    props: {
                        title: res.data.message,
                        icon: 'XCircle',
                        variant: 'success',
                    },
                })
                this.$router.push({ name: 'ownerResident' })
                // return true
            } catch (err) {
                this.$toast({
                    component: ToastificationContent,
                    props: {
                        title: err.response.data.message,
                        icon: 'XCircle',
                        variant: 'danger',
                    },
                })
                return false
            }
        },
        async validationFormApartmentUnitOwner() {
            // // eslint-disable-next-line no-constant-condition, no-self-compare
            // if (1 === 1) {
            //   return true
            // }

            const isSuccess = await this.$refs.apartmentUnitOwnerRules.validate()
            // console.log('apartmentUnitOwnerRules', isSuccess)
            if (!isSuccess) {
                return false
            }

            try {
                const data = []
                this.liteStructureGet.map(item => item.units.map(unitItem => {
                    if (
                        unitItem.data.owner.mobile !== ''
                        && unitItem.data.owner.full_name !== ''
                    ) {
                        data.push({ ...unitItem.data.owner })
                    }
                    if (
                        unitItem.data.tenant.mobile !== ''
                        && unitItem.data.tenant.full_name !== ''
                    ) {
                        data.push({ ...unitItem.data.tenant })
                    }

                    //

                    return ''
                }))

                const res = await this.$http.post(
                    'https://sm.oop.ir/api/v1/user/lite-register-store',
                    data,
                    {
                        headers: {
                            'Authorization': 'Bearer ' + localStorage.getItem('token'),
                        },
                    }
                )
                this.$toast({
                    component: ToastificationContent,
                    props: {
                        title: res.data.message,
                        icon: 'XCircle',
                        variant: 'success',
                    },
                })
                const liteUserGet = await this.$http.get(
                    'https://sm.oop.ir/api/v1/user/lite-register-get',
                )
                liteUserGet.data.data.forEach(item => {
                    item.units.forEach(unitItem => {
                        unitItem.users.forEach(unitUser => {
                            // eslint-disable-next-line no-param-reassign
                            unitUser.debt = ''
                        })
                    })
                })
                this.liteUserGet = liteUserGet.data.data
                // console.log('the data is: ', data)
                return true
            } catch (err) {
                return false
            }
        },
        async validationFormApartmentUnitDebt() {
            const isSuccess = await this.$refs.apartmentUnitDebt.validate()
            // console.log('apartmentUnitDebt', isSuccess)
            if (!isSuccess) {
                return false
            }
            // // eslint-disable-next-line no-constant-condition, no-self-compare
            // if (1 === 1) {
            //   return false
            // }

            try {
                // console.log(this.liteUserGet)
                const data = []
                this.liteUserGet.forEach(floor => {
                    floor.units.forEach(unit => {
                        // unit.unit_id
                        unit.users.forEach(user => {
                            data.push({
                                unit_id: unit.unit_id,
                                debt: user.debt,
                                user_id: user.user_id,
                            })
                        })
                    })
                })
                const res = await this.$http.post(
                    'https://sm.oop.ir/api/v1/document/lite-first-document-store',
                    data,
                    {
                        headers: {
                            'Authorization': 'Bearer ' + localStorage.getItem('token'),
                        },
                    }
                )
                this.$toast({
                    component: ToastificationContent,
                    props: {
                        title: res.data.message,
                        icon: 'XCircle',
                        variant: 'success',
                    },
                })

                // console.log(data)

                return true
            } catch (err) {
                return false
            }
        },
        goToNextStep() {
            // todo: prevent next tab in mobile is invalid
            // console.log(this.$refs.mobileInputRef)
            this.$refs.formWizardNumRef.nextTab()
            setTimeout(() => {
                this.activeTabIndex = this.$refs.formWizardNumRef.activeTabIndex
            }, 100)
        },
        prevTabStep() {
            this.$refs.formWizardNumRef.prevTab()
        },
        resetApartmentFloors(floors) {
            // eslint-disable-next-line radix
            // if (Number.isNaN(parseInt(floors))) {
            //   return
            // }
            this.form.apartment.unitsPerFloor = []
            // eslint-disable-next-line no-plusplus
            for (let i = 1; i <= floors; ++i) {
                this.form.apartment.unitsPerFloor.push({
                    floor: i,
                    from: '',
                    to: '',
                })
            }
            // console.log(this.form.apartment.unitsPerFloor)
        },
        async addApartmentFloors() {
            this.floorsName = []
            if (this.form.apartment.floorsStartAt.value === -1) {
                this.floorsName.push('زیرزمین')
            }
            if (this.form.apartment.floorsStartAt.value <= 0) {
                this.floorsName.push('همکف')
            }
            console.log(this.form.apartment.floors, this.form.apartment.floorsStartAt.value)
            for (let i = 1; i <= parseInt(this.form.apartment.floors) + this.form.apartment.floorsStartAt.value - 1; i += 1) {
                console.log(i)
                this.floorsName.push(`طبقه` + i)
            }
            console.log(this.floorsName)
            // const isSuccess = await this.$refs.unitRulesFirstStep.validate()
            //
            // if (!isSuccess) {
            //   return false
            // }

            this.resetApartmentFloors(this.form.apartment.floors)
            this.showFloorsDetail = true
            // return true
        },
        setUnitsPerFloorAndContinue() {
            // unitsPerFloor = [{floor, from, to}]
            // ownersInfo = [{floor, unit, ownerName, ownerMobile, isOwner, tenantName, tenantMobile}]
            // this.form.apartment.ownersInfo = []
            // this.form.apartment.unitsPerFloor.map(item => {
            //   for (let i = +item.from; i <= +item.to; i += 1) {
            //     console.log(`index ${i} adding ownersInfo to each unit of floor`)
            //     this.form.apartment.ownersInfo.push({
            //       floor: item.floor,
            //       unit: i,
            //       ownerName: null,
            //       ownerMobile: null,
            //       isOwner: false,
            //       tenantName: null,
            //       tenantMobile: null,
            //       debt: null,
            //     })
            //   }
            //   return null
            // })
            this.goToNextStep()
        },
        getIndexOfOwner(unitNumber) {
            return this.liteStructureGet.findIndex(x => x.unit === unitNumber)
        },
    },
}
</script>
