  <div class="auth-wrapper auth-v2">
    <b-row class="auth-inner m-0">
      <!-- Left Text-->
      <b-col
        xl="7"

        class="d-none d-xl-flex align-items-center p-5"
      >
        <div
          class="w-100 d-xl-flex align-items-center justify-content-center px-5"
        >
          <b-img
            fluid
            :src="imgUrl"
            alt="Login V2"
          />
        </div>
      </b-col>
      <!-- /Left Text-->

      <!-- Login-->

      <b-col
        lg="12"
        xl="5"
        class="d-flex align-items-center auth-bg p-lg-5 max-w-set"
        style="padding: 0;"
      >
        <b-col
          sm="8"
          md="6"
          lg="12"
          class="px-xl-2 mx-auto"
        >
          <!-- Brand logo-->
          <div
            style="
              display: flex;
              justify-content: space-between;
              align-items: center;
            "
          >
            <b-link class="logo mt-2 text-right justify-content-start">
              <span style="margin-right: -15px">
                <vuexy-logo />
              </span>
              <span class="text-primary brand-name"> شارژ مستر </span>
            </b-link>
            <a
              v-if="!isFirstStep"
              href="#"
              @click.prevent="goToBackStep"
            >
              <img
                :src="require('@/assets/images/arrow-left-svg.svg')"
                style="margin: 25px 0 0 20px; transform: rotate(180deg);"
              >
            </a>
          </div>
          <FormWizardNumber
            ref="formWizardNumberComponent"
            @stepChange="setStepChanged"
          />
        </b-col>
      </b-col>
    </b-row>
  </div>
