<script>
/* eslint-disable global-require */
// import { ValidationProvider, ValidationObserver } from 'vee-validate'
import VuexyLogo from '@core/layouts/components/Logo.vue'
import {
  BRow,
  BCol,
  BLink,
  /* BCardText,
  BCardTitle, */
  BImg,
} from 'bootstrap-vue'
import { required, mobile } from '@validations'
import { togglePasswordVisibility } from '@core/mixins/ui/forms'
import store from '@/store/index'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'
import axios from '@/libs/axios'
import FormWizardNumber from '@/@core/components/form-wizard/FormWizardNumber.vue'

export default {
  components: {
    BRow,
    BCol,
    BLink,
    /* BCardText,
    BCardTitle, */
    BImg,
    VuexyLogo,
    FormWizardNumber,
  },
  mixins: [togglePasswordVisibility],
  data() {
    return {
      isFirstStep: true,
      currentStep: '',
      showMobile: true,
      status: '',
      otp: '',
      userMobile: '',
      buildingList: null,
      selectedBuilding: null,
      chooseBuilding: false,
      sideImg: require('@/assets/images/pages/login-v2.svg'),
      // validation rulesimport store from '@/store/index'
      required,
      mobile,
      name: null,
    }
  },
  computed: {
    imgUrl() {
      if (store.state.appConfig.layout.skin === 'dark') {
        // eslint-disable-next-line vue/no-side-effects-in-computed-properties
        this.sideImg = require('@/assets/images/pages/login-v2-dark.svg')
        return this.sideImg
      }
      return this.sideImg
    },
  },
  mounted() {},
  methods: {

    setStepChanged(val) {
      if (val > 0) {
        this.isFirstStep = false
      } else {
        this.isFirstStep = true
      }
      this.currentStep = val
    },

    goToBackStep() {
      this.$refs.formWizardNumberComponent.prevTabStep()
      // console.log(this.$refs.formWizardNumberComponent.activeTabIndex)
    },

  },
}
</script>

